@import "utils/vars";

.process {
  max-width: $max-width-bg;
  box-sizing: border-box;

  margin: 0 auto;
  padding: 160px $side-padding 0;

  display: flex;
  justify-content: space-between;

  background-color: #fcfcfd;

  &__article {
    flex-shrink: 0;
  }

  &__title {
    margin-bottom: 19px;
  }

  &__label {
    font-weight: 900;
    font-size: 32px;
    color: $c-blue;
    line-height: 1.4;

    margin-bottom: 64px;
  }

  &__paragraph {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    color: $c-dark-blue;
    mix-blend-mode: normal;
  }
}

@media (min-width: 1921px) {
  .process {
    padding: 160px $side-padding-big-res 0;
  }
}

@media (max-width: $tablet-view) {
  .process {
    &__article {
      margin-right: 20px;
    }
  }
}

@media (max-width: $mobile-view-big) {
  .process {
    flex-direction: column;
    align-items: center;
    padding: 0 $side-padding;

    &__article {
      margin-right: 0;
    }
  }
}
